   const lista =  document.querySelector('.carousel__lista');
   if(lista){
	
	var glider = new Glider(document.querySelector('.carousel__lista'), {
		slidesToShow: 1, // empieza como modo celular . muestra el tamaño de la imagen
		slidesToScroll: 1, // cuando le das siguiente los pasos que va dar
		draggable: true, // arrastable
		dots: '.carousel__indicadores',  // indicador de abajo
		arrows: {
			prev: '.carousel__anterior',  //boton anterior
			next: '.carousel__siguiente' //boton siguiente
		},
		responsive: [ //responsive
			{
			  // screens greater than >= 1024px
			  breakpoint: 800,
			  settings: {
				slidesToShow: 2,
				slidesToScroll: 1,
				duration: 0.5,
			  }
			}
			,{
			  // screens greater than >= 1024px
			  breakpoint: 1201,
			  settings: {
				slidesToShow: 4,
				slidesToScroll: 1,
				duration: 0.5,
			  }
			}
		]
	});

		// funcion automatico
	function sliderAuto(slider, miliseconds) {
		const slidesCount = slider.track.childElementCount;
		let slideTimeout = null;
		let nextIndex = 1;
	   
		function slide () {
		  	slideTimeout = setTimeout(
			function () {
			  if (nextIndex >= slidesCount ) {
				nextIndex = 0;
			  }
			  slider.scrollItem(nextIndex++);
			},
			miliseconds
		  );
		}
	   
		slider.ele.addEventListener('glider-animated', function() {
		  window.clearInterval(slideTimeout);
		  slide();
		});
	   
		slide();
	}
	   sliderAuto(glider, 3000)
   }